@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Open Sans Regular"), local("OpenSans-Regular"), url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Open Sans Bold"), local("OpenSans-Bold"), url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

body {
  display: flex;
  flex-direction: column;
  min-width: 260px;
  min-height: 100vh;
  background-color: #ffffff;
  font-size: 15px;
  line-height: 24px;
  font-family: "Open Sans", "Arial", sans-serif;
  color: #000000;
  overflow-x: hidden; }

.visually-hidden {
  width: 1px;
  height: 1px;
  position: absolute;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

/* *******************
*** 2. Main styles ***
******************* */
.main-content {
  width: 100vw;
  min-height: 100vh;
  background-color: #f5f5f5; }

.main-content__main-heading {
  display: inline-block;
  width: auto;
  margin: 0;
  margin-top: 40px;
  margin-left: 80px;
  padding-left: 10px;
  padding-bottom: 10px;
  font-size: 40px;
  font-weight: 400;
  line-height: 50px;
  border-bottom: 4px double #000000;
  border-left: 4px double #000000; }

.game {
  display: flex;
  justify-content: center;
  margin-top: 60px; }

.game__tiles {
  box-shadow: 0 2px 5px 1px rgba(0, 0, 0, 0.5); }

.game__difficulty {
  margin-left: 70px; }

.game__difficulty-heading {
  padding-bottom: 5px;
  font-size: 25px;
  font-weight: 700;
  line-height: 35px;
  border-bottom: 2px dashed #000000; }

.game__start-button {
  width: 130px;
  min-height: 45px;
  margin-top: 50px;
  font-size: 25px;
  line-height: 30px;
  color: #000000;
  background-color: #00ff00;
  border: 1px solid #000000;
  border-radius: 30px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.5);
  transition-duration: 0.05s;
  transition-timing-function: ease;
  transition-property: color, background-color; }

.game__loss-message {
  display: none;
  width: 100%;
  min-height: 50px;
  font-size: 30px;
  font-weight: 700;
  line-height: 40px;
  text-align: center;
  border-top: 4px double #000000;
  border-bottom: 4px double #000000; }
  .game__loss-message--show {
    display: block; }

.game__start-button:hover,
.game__start-button:focus {
  background-color: #32cd32; }

.game__start-button:active {
  color: #ffffff;
  background-color: #228b22; }

.tiles {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 500px;
  height: 500px;
  background-color: #dda0dd;
  border-radius: 50%; }

.tiles__group {
  display: flex;
  margin: 0;
  padding: 0; }
  .tiles__group--top {
    margin-top: auto; }
  .tiles__group--bottom {
    margin-bottom: auto; }

.tiles__item {
  width: 248px;
  height: 248px;
  margin: 0;
  border: 1px solid #dda0dd;
  transition: 0.1s ease opacity;
  cursor: pointer; }
  .tiles__item--top-left {
    background-color: #e6e6fa;
    border-top-left-radius: 248px;
    border-bottom-right-radius: 20px; }
  .tiles__item--top-right {
    background-color: #ffe4e1;
    border-top-right-radius: 248px;
    border-bottom-left-radius: 20px; }
  .tiles__item--bottom-left {
    background-color: #fafad2;
    border-bottom-left-radius: 248px;
    border-top-right-radius: 20px; }
  .tiles__item--bottom-right {
    background-color: #98fb98;
    border-bottom-right-radius: 248px;
    border-top-left-radius: 20px; }

.tiles__item:hover,
.tiles__item:focus {
  opacity: 0.85; }

.tiles__item:active {
  opacity: 0.6; }

.difficulty-levels {
  margin: 0;
  padding: 0;
  padding-left: 20px; }
